import { WINDOW_ACTIVITY_DETECTOR_IS_ACTIVE_KEY } from "..";
import { JOB_DELAY_EVENT_PREFIX } from "../../types";
/**
 * Delay a job with the defined `delay_ms`.
 */

function jobDelay(job) {
  const isIdle = !!window[WINDOW_ACTIVITY_DETECTOR_IS_ACTIVE_KEY];
  const {
    type,
    delay_ms
  } = job;
  const settings = {
    delay_ms
  };
  document.dispatchEvent(new CustomEvent("".concat(JOB_DELAY_EVENT_PREFIX).concat(type), {
    detail: {
      job,
      isIdle,
      settings
    }
  }));
  return new Promise(resolve => setTimeout(resolve, settings.delay_ms));
}

export { jobDelay };