import { STATUS_ADDITIONAL_DATA_EVENT } from "../types";
import { STATUS_EVENT } from "../types";
import { request } from "../utils";
import { locationRestStatusGet } from "../wp-api";
import { getGlobalQueue, refreshQueue } from ".";
let timeout;
let currentlyFetching = false;
/**
 * Fetch the status of the complete queue in a given interval. In general, the queue pulls always
 * the status, but you could change the interval to provide a more "up-to-date" UI to your users.
 *
 * You need to listen to the fetch-status event.
 */

async function fetchStatus(intervalOrImmediate = 15000, immediate = false) {
  const fn = async () => {
    if (currentlyFetching) {
      // There is still a request, skip this interval
      return;
    }

    if (!window.realQueueWorker && document.visibilityState === "hidden") {
      // This tab is currently not working, skip status if tab is inactive
      return;
    }

    currentlyFetching = true;

    try {
      const settings = {
        additionalData: []
      };
      document.dispatchEvent(new CustomEvent(STATUS_ADDITIONAL_DATA_EVENT, {
        detail: {
          settings
        }
      }));
      const detail = await request({
        location: locationRestStatusGet,
        params: {
          additionalData: settings.additionalData.join(",")
        }
      });
      document.dispatchEvent(new CustomEvent(STATUS_EVENT, {
        detail
      })); // Check if the queue is currently empty, but there are still remaining items, try to get them

      const {
        remaining,
        errors
      } = detail;

      if ( // Only the worker should refresh the queue as needed
      window.realQueueWorker && getGlobalQueue().size === 0 && Object.values(remaining).length > 0 && // Do not load new items to the queue until the errors are resolved
      Object.values(errors.list).length === 0) {
        refreshQueue(false);
      }
    } finally {
      currentlyFetching = false;
    }
  };

  if (typeof intervalOrImmediate === "number") {
    clearInterval(timeout);
    timeout = setInterval(() => fn(), intervalOrImmediate);
    immediate && (await fn());
  } else if (intervalOrImmediate === true) {
    await fn();
  }
}

export { fetchStatus };