import { getStaticOptions, localStorageTest } from "../../utils";
import { LOCAL_STORAGE_KEY_QUEUE_NOTIFY_WORKER_PREFIX } from ".";
/**
 * Clear all worker notifications.
 */

function clearWorkerNotifications() {
  if (localStorageTest()) {
    const lsPrefix = "".concat(LOCAL_STORAGE_KEY_QUEUE_NOTIFY_WORKER_PREFIX).concat(getStaticOptions().localStorageSuffix);

    for (const lsEntryName of Object.keys(localStorage)) {
      if (lsEntryName.startsWith(lsPrefix)) {
        localStorage.removeItem(lsEntryName);
      }
    }

    return true;
  }

  return false;
}

export { clearWorkerNotifications };