import PQueue from "p-queue";
import { RealQueueClass } from ".";

/**
 * Create a page-wide queue saved in `window`.
 */
function getGlobalQueue() {
  if (!window.realQueueInstance) {
    const queue = new PQueue({
      autoStart: true,
      concurrency: 1,
      queueClass: RealQueueClass
    }); // By default, the p-queue does not expose the queue class

    queue.enqueueHandler = function () {
      return this._queue;
    }; // This is a bit hacky, but currently the only way to access the parent instance (?!)


    window.realQueueInstance = queue;
    window.realQueueInstance.enqueueHandler().setHandler(window.realQueueInstance);
  }

  return window.realQueueInstance;
}

export { getGlobalQueue };